<template>
  <div>
    <span class="glyphicon glyphicon-option-vertical" @click="changeSettingsVisibility"></span>
    <div id="settings" :class="{collapse: !isVisible}">
      <div class="checkbox">
        <label>
          <input type="checkbox" ref="attentionSoundController" :checked="isUsingAttentionSound" @click="changeAttentionSound" data-toggle="toggle" v:model="isUsingAttentionSound">Sound attention gong
        </label>
      </div>
      <div :class="{collapse: !isUsingAttentionSound}">
        <label>Every <input class="attentionInterval" v-model="attentionInterval" type="number"> Min.</label>
      </div>
      <div>
        <a class="btn btn-default" @click="saveSettings">Save settings</a>
      </div>  
    </div>
  </div>
</template>

<script>

export default {
  name: 'settings',
  components: {
  },
  mounted(){
    var self = this;
    var attentionSoundController = window.$(self.$refs.attentionSoundController);
    attentionSoundController.change(function(e) {
      var checked = window.$(this).prop('checked');
      self.isUsingAttentionSound = checked;
    });
  },
  data () {
    return {
      isVisible: false,
      isUsingAttentionSound: localStorage.timerAttentionSound || false,
      attentionInterval: localStorage.timerAttentionSoundInterval || 5
    }
  },
  methods: {
    changeSettingsVisibility(){
      var self = this;
      self.isVisible = !self.isVisible;
    },
    changeAttentionSound(){
      this.$refs.attentionSoundController;
    },
    saveSettings(){
      var self = this;
      self.isVisible = false;
      localStorage.timerAttentionSound = self.isUsingAttentionSound;
      localStorage.timerAttentionSoundInterval = self.attentionInterval;
    }
  }
}

</script>

<style scoped>
  #settings{
    position: absolute;
    top: 8px;
    left:40px;
    z-index: 5;
    background: #FFE4E1;
    border: 1px solid #808080;
    padding-left: 20px;
    padding-right: 20px;
  }

  .btn{
    margin-top: 20px;
    margin-bottom: 10px;
    margin-left: 100px;
  }

  .glyphicon-option-vertical{
    font-size: 24px;
    cursor: pointer;
    position: absolute;
    top: 15px;
    left: 15px;
  }

  .attentionInterval{
    padding-left: 5px;
    margin-right: 5px;
    width: 40px;
  }

  label{
    font-size: 16px;
    font-family: 'Sofia';
  }
</style>



